import Vue from 'vue'
import Router from 'vue-router'
import Index from 'components/Index'
import Hotel from 'components/Hotel'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
	{
      path: '/Hotel',
      name: 'Hotel',
      component: Hotel
    }
  ]
})



